import store from "../../store";
import TargetRow from "./TargetRow";
import {connect} from "react-redux";
import ReloadButton from "./ReloadButton";
import { updateAppSettings } from "../../actions/foundationActions";
import { randNum } from "../../utilities/utilityFunctions";
import GoalBox from "./GoalBox";

const ExecuteChecklist = () => {
    let currentCheckList = store.getState().siteModelChecklist?.currentChecklist;
    let targets = store.getState().siteModelChecklist?.currentChecklistTargets ? store.getState().siteModelChecklist?.currentChecklistTargets : [];
    let goals = store.getState().siteModelChecklist?.currentChecklistGoals ? store.getState().siteModelChecklist.currentChecklistGoals : [];
    let results = store.getState().siteModelChecklist?.currentChecklistResults ? store.getState().siteModelChecklist.currentChecklistResults : [];

    let topChilds = targets?.filter((targ)=>{
        return targ.parentid===0;
    })

    return <div>
        <div className={"row"}>
            <div className={"col-9"}>
            <h2>{currentCheckList?.name}</h2>
            </div>
            <div className={"col-3"}>
                <ReloadButton />
            </div>
            </div>
        <table className={"table table-striped table-bordered"}>
            <thead>
            <tr>
                <th></th>
                <th>Target</th>
                <th>Display</th>
                {goals
                    .filter((item)=>{
                        return item.label?.length
                    })
                    .map((item)=>{
                        return <GoalBox goal={item} />
                    })
                }
                <th><button className="btn btn-sm" onClick={()=>{store.dispatch(updateAppSettings({showDefaultModal:1,"modalModuleName":"components/cbChecklist/GoalForm","modalTitle":"New Goal"}))}} title="Add Goal"> + </button></th>
                <th></th>
            </tr>
            </thead>
            <tbody>
                {topChilds
                    .map((item)=>{
                        return <TargetRow target={item} key={item.checklistuuid + item.testpackage} goals={goals} results={results} depth={0} numOptions={topChilds.length+1} />
                    })
                }
                <TargetRow target={{"checklistuuid":currentCheckList.id,parentid:0}} key={"NewTargetRow"} goals={goals} results={results} depth={0} numOptions={topChilds.length+1} />
            </tbody>
        </table>

    </div>
}

const mapStateToProps = (state) =>{
    return {
        siteModelChecklist:state.siteModelChecklist
    };
};

export default connect(mapStateToProps,{})(ExecuteChecklist);